:root {
  --active-color: #00acdc;
}

body {
  background-color: #e5e5e5;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  align-items: center;
  display: flex;
}

.nav-link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  max-width: 1200px;
  margin: 0 auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
  list-style: none;
}

.nav-item:last-child {
  margin-right: 0;
}

.header-logo {
  align-items: center;
  height: 40px;
  display: flex;
}

.nav-link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  margin: 0;
  position: absolute;
  bottom: 0;
}

.nav-link.is-active, .nav-link:hover {
  color: var(--active-color);
}

.header-logo-img {
  max-height: 40px;
  margin: 0;
  padding: 0;
  display: block;
}

.visually-hidden {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}
